@import "https://fonts.googleapis.com/css2?family=Lato:wght@400;700&display=swap";
@font-face {
  font-family: boxicons;
  font-weight: normal;
  font-style: normal;
  src: url("boxicons.e1bbac92.eot");
  src: url("boxicons.e1bbac92.eot") format("embedded-opentype"), url("boxicons.dc49189e.woff2") format("woff2"), url("boxicons.b6bc5593.woff") format("woff"), url("boxicons.6b032104.ttf") format("truetype"), url("boxicons.b06fc8ec.svg#boxicons") format("svg");
}

.bx {
  font-variant: normal;
  text-rendering: auto;
  text-transform: none;
  speak: none;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-style: normal;
  font-weight: normal;
  line-height: 1;
  display: inline-block;
  font-family: boxicons !important;
}

.bx-ul {
  margin-left: 2em;
  padding-left: 0;
  list-style: none;
}

.bx-ul > li {
  position: relative;
}

.bx-ul .bx {
  font-size: inherit;
  line-height: inherit;
  width: 2em;
  text-align: center;
  position: absolute;
  left: -2em;
}

@keyframes spin {
  0% {
    -webkit-transform: rotate(0);
    transform: rotate(0);
  }

  100% {
    transform: rotate(359deg);
  }
}

@keyframes burst {
  0% {
    opacity: 1;
    transform: scale(1);
  }

  90% {
    opacity: 0;
    transform: scale(1.5);
  }
}

@keyframes flashing {
  0% {
    opacity: 1;
  }

  45% {
    opacity: 0;
  }

  90% {
    opacity: 1;
  }
}

@keyframes fade-left {
  0% {
    opacity: 1;
    transform: translateX(0);
  }

  75% {
    opacity: 0;
    transform: translateX(-20px);
  }
}

@keyframes fade-right {
  0% {
    opacity: 1;
    transform: translateX(0);
  }

  75% {
    opacity: 0;
    transform: translateX(20px);
  }
}

@keyframes fade-up {
  0% {
    opacity: 1;
    transform: translateY(0);
  }

  75% {
    opacity: 0;
    transform: translateY(-20px);
  }
}

@keyframes fade-down {
  0% {
    opacity: 1;
    transform: translateY(0);
  }

  75% {
    opacity: 0;
    transform: translateY(20px);
  }
}

@-webkit-keyframes tada {
  from {
    transform: scale3d(1, 1, 1);
  }

  10%, 20% {
    transform: scale3d(.95, .95, .95)rotate3d(0, 0, 1, -10deg);
  }

  30%, 50%, 70%, 90% {
    transform: scale3d(1, 1, 1)rotate3d(0, 0, 1, 10deg);
  }

  40%, 60%, 80% {
    transform: scale3d(1, 1, 1)rotate3d(0, 0, 1, -10deg);
  }

  to {
    transform: scale3d(1, 1, 1);
  }
}

@keyframes tada {
  from {
    transform: scale3d(1, 1, 1);
  }

  10%, 20% {
    transform: scale3d(.95, .95, .95)rotate3d(0, 0, 1, -10deg);
  }

  30%, 50%, 70%, 90% {
    transform: scale3d(1, 1, 1)rotate3d(0, 0, 1, 10deg);
  }

  40%, 60%, 80% {
    transform: rotate3d(0, 0, 1, -10deg);
  }

  to {
    transform: scale3d(1, 1, 1);
  }
}

.bx-spin, .bx-spin-hover:hover {
  animation: spin 2s linear infinite;
}

.bx-tada, .bx-tada-hover:hover {
  animation: tada 1.5s infinite;
}

.bx-flashing, .bx-flashing-hover:hover {
  animation: flashing 1.5s linear infinite;
}

.bx-burst, .bx-burst-hover:hover {
  animation: burst 1.5s linear infinite;
}

.bx-fade-up, .bx-fade-up-hover:hover {
  animation: fade-up 1.5s linear infinite;
}

.bx-fade-down, .bx-fade-down-hover:hover {
  animation: fade-down 1.5s linear infinite;
}

.bx-fade-left, .bx-fade-left-hover:hover {
  animation: fade-left 1.5s linear infinite;
}

.bx-fade-right, .bx-fade-right-hover:hover {
  animation: fade-right 1.5s linear infinite;
}

