.scrollup {
  position: fixed;
  right: 1rem;
  bottom: -30%;
  background-color: #fff;
  box-shadow: 0 4px 12px hsla(206, 4%, 20%, 0.1);
  display: inline-flex;
  padding: 0.35rem;
  border-radius: 0.15rem;
  font-size: 1.25rem;
  color: $title-color;
  z-index: $z-tooltip;
  opacity: 0.9;
  transition: 0.4s;
  &:hover {
    transform: translateY(0.25rem);
    opacity: 1;
  }
}

.show-scroll {
  bottom: 3rem;
}

.active-link {
  color: #fff;
}

// ======== loader ==========

.load {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: hsl(0, 0%, 99%);
  display: grid;
  place-items: center;
  z-index: 1000;
}

.load__gif {
  width: 210px;
}
